.header {
display: flex;
justify-content: space-between;
align-items: flex-start;
padding: 1rem;
position: fixed;
width: 100%;
height: 90px;
z-index: 10;
}

.header-bg {
    background-color: rgba(0, 0, 0, 0.85);
    transition: 0.5s;
}
.nav-menu {
    display: flex;
}
.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
}

@media screen and (max-width:1040px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s ;
    }

    .nav-menu.active {
        left: 0;
    }

    .nav-menu li {
        padding: 1rem 0;
        background-color: rgb(42, 42, 42);
        width: 100%;
        text-align: center;
        margin-top: 15px;
        transition: 0.2s all ease;
        cursor: pointer;
    }
    .nav-menu li:hover {
        background-color: rgb(119, 116, 116);
        border-bottom: 1px solid white;
        
    }

    .nav-menu li a {
        font-size: 2rem;
    }

    

    .hamburger {
        display: initial;
        cursor: pointer;
    }

    
}